  font-family: Roboto, Arial, sans-serif;
  position: relative;
}
.tabs__wrapper .tab__item__title .current {
  background-color: rgba(0,91,185,0.08);
  width: 32px;
  height: 32px;
  text-align: center;
  font-size: 16px;
  line-height: 21px;
  color: #005bb9;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 101px;
}
.tabs__wrapper .tab__item__content {
  opacity: 0;
  max-height: 206px;
}
.tabs__wrapper .tab__item__content .cards__wrapper {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: row;
  flex-wrap: wrap;
}
.tabs__wrapper .tab__item__content .card__wrapper-small {
  margin-right: 24px;
