  margin-right: 0;
}
.tabs__wrapper .tab__item__content .card__wrapper-small:last-child,
.tabs__wrapper .tab__item__content .card__wrapper-small:nth-last-child(2) {
  margin-bottom: 0;
}
.tabs__wrapper .tab__item:after {
  content: '';
  display: block;
  position: absolute;
  top: 24px;
  right: 15px;
  width: 6px;
  height: 6px;
  border: 1px solid rgba(51,51,51,0.6);
  border-top-color: transparent;
  border-left-color: transparent;
  transform: rotate(-45deg);
  transform-origin: 6px 6px 6px;
  transition: transform 0.4s;
}
.tabs__wrapper .tab__item.active {
  height: auto;
  max-height: 290px;
  overflow-y: auto;
  cursor: auto;
  padding-bottom: 4px;
  position: relative;
}
.tabs__wrapper .tab__item.active .tab__item__title {
  margin-bottom: 19px;
}
.tabs__wrapper .tab__item.active .tab__item__content {
  opacity: 1;
  position: relative;
}
.tabs__wrapper .tab__item.active .text__title {
  font-size: 18px;
  line-height: 138%;
  font-family: Roboto-Bold, Arial, sans-serif;
  font-weight: bold;
  color: rgba(51,51,51,0.78);
  margin-bottom: 11px;
}
.tabs__wrapper .tab__item.active .text__wrapper {
  font-size: 16px;
  line-height: 138%;
  font-family: Roboto-Bold, Arial, sans-serif;
  color: rgba(51,51,51,0.78);
  margin-bottom: 35px;
}
.tabs__wrapper .tab__item.active .button__img {
  display: flex;
