  fill: #fff;
}
.tabs__wrapper .tab__item.active .button__img .button__text {
  display: none;
}
.tabs__wrapper .tab__item.active:after {
  transform: rotate(45deg);
}
.tabs__wrapper .tab__item:hover:after {
  border-right-color: #005bb9;
  border-bottom-color: #005bb9;
}
.tabs__wrapper .tab__items__mob {
  display: none;
